$prefix: '.wc-booster';

#{$prefix}-wishlist-button-wrapper {
	display: flex;
	gap: 10px;
    z-index: 99;
 
	#{$prefix}-wishlist-button{
		padding: 0;
		background: transparent;
		border: none;
		cursor: pointer;
		position: relative;
		
		i{
			font-weight: 900;
		}

		&.added {
			i{
				color: #e53737;
				font-weight: 900;

				&:before {
					content: "\f004";
				}
			}
		}

		&.loading {
			padding-right: 0 !important;
			&:after {
				
				border-top: 2px solid #000000;
				width: 8px;
				height: 8px;				
				right: 10px;
				top: 8px;
			}
		}
	}
}


.products-block-post-template {
	li.product{
		position: relative;
	}
}
.wp-block-post-template{
	li.type-product{
		position: relative;
	}
}

.single-product{
	.wp-block-column{
		position: relative;
	}
}